<template>
  <section class="echartBox">
      <div ref="echartContent" class="echartContent"></div>
      <v-swiper v-bind:swPic="swHeight" v-bind:minVal="minWidth" v-bind:diyType="'card'"></v-swiper>
  </section>
</template>

<script>
import swiper from '@/components/common/swiper.vue'
import echarts from '../../../static/echarts.min'
// 这里之所以使用 require 而不是 import，是因为 require 可以直接从 node_modules 中查找，而 import 必须把路径写全。
export default {
    name: 'echarts',
    data () {
      return {
        minWidth: 2,
        swHeight: this.$store.getters.showType
      }
    },
    created () {
    },
    mounted () {
     const option = {
        title: {
          text: ''
        },
        legend: {
          top: 0,
          data: ['标准', '年中', '年末']
        },
        tooltip: {},
        radar: {
          axisLine: {
            show: true
          },
          splitArea: {
            areaStyle: {
              color: '#fff',
              shadowBlur: 0
            }
          },
          radius:'70%',
          splitNumber: 8,
          nameGap:5,
          // shape: 'circle',
          indicator: [{
            name: '身高\n(cm)',
            max: 6500,
            color:'#000',
          },
            {
              name: 'BMI',
              max: 30000,
              color:'#000',
            },
            {
              name: '体前屈\n(cm)',
              max: 38000,
              color:'#000',
            },
            {
              name: '单脚站\n(s)',
              max: 52000,
              color:'#000',
            },
            {
              name: '投掷\n(m)',
              max: 25000,
              color:'#000',
            },
            {
              name: '立定跳\n(m)',
              max: 25000,
              color:'#000',
            },
            {
              name: '跳绳\n(个/min)',
              max: 25000,
              color:'#000',
            },
            {
              name: '十字跳\n(次/秒)',
              max: 25000,
              color:'#000',
            },
            {
              name: '505跑\n(秒)',
              max: 25000,
              color:'#000',
            },
            {
              name: '体重\n(kg)',
              max: 26000,
              color:'#000',
            }
          ]
        },
        series: [
          {
            name: '',
            type: 'radar',
            symbol: "circle",
            symbolSize:"10",
            data: [
              {
                value: [3200, 18900, 19000, 26000, 9500, 12500, 9500, 15500, 12500, 13000],
                name: '标准',
                itemStyle: {
                  normal: {
                    color : "rgba(0,161,233,.6)", // 图表中各个图区域的边框线拐点颜色
                    lineStyle: {
                      color:"#00A1E9", // 图表中各个图区域的边框线颜色
                      borderColor: "#00A1E9"
                    }
                  }
                },
                areaStyle: {
                  normal: {
                    opacity: 0.3,
                  }
                },
              },
              {
                value: [2450, 18800, 23600, 32200, 12500, 12500, 6100, 15500, 12500, 16000],
                name: '年中',
                symbol: "circle",
                symbolSize:"10",
                itemStyle: {
                  normal: {
                    color : "rgba(248,129,37,.6)", // 图表中各个图区域的边框线拐点颜色
                    lineStyle: {
                      color:"#F88125", // 图表中各个图区域的边框线颜色
                      borderColor: "#F88125"
                    }
                  }
                },
                areaStyle: {
                  normal: {
                    opacity: 0.3,
                  }
                }
              },
              {
                value: [4900, 22600, 28600, 34000, 17000, 9000, 20000, 16500, 19000, 13000],
                name: '年末',
                symbol: "circle",
                symbolSize:"10",
                itemStyle: {
                  normal: {
                    color : "rgba(141,206,16,.6)", // 图表中各个图区域的边框线拐点颜色
                    lineStyle: {
                      color:"#8CCC2F", // 图表中各个图区域的边框线颜色
                      borderColor: "#8CCC2F"
                    }
                  }
                },
                areaStyle: {
                  normal: {
                    opacity: 0.3,
                  }
                }
              }
            ]
          }]
      }
      let myChart = echarts.init(this.$refs.echartContent)
      myChart.setOption(option)
      this.showName()
    },
    components: {
      'v-swiper': swiper
    },
    methods: {
      showName () {
        // this.$store.commit('changeType', {id: 2})
        // this.minWidth = this.$store.getters.showType
      }
    }
  }
</script>

<style lang="scss" scoped>
 .echartContent{
    min-height: 420px;
    width: 100%;
   padding-top: 5%;
  }
  .echartBox{
    height: 100%;
    /deep/ .el-carousel{
      position: fixed;
      bottom: 50px;
    }
    /deep/ .el-carousel__indicators--outside{
      display: none;
    }
  }
</style>



