<template>
  <div id="flBox">
    <transition
    @before-enter="bEnter"
    @enter="enter"
    @after-enter="aEnter"
    @enter-cancelled="enterCanc"
    >
        <router-view></router-view>
    </transition>
  </div>
</template>

<script>
import '../../static/three.min'
import '../../static/Detector'
export default {
  name: 'login',
  data () {
    return {
    }
  },
  mounted () {

  },
  methods: {
    bEnter(el){
      el.style.transform = "translate(100% ,-200%) scale(0.1) "
    },
    enter(el, done){
      el.offsetTop;
      el.style.transform = 'translate(0,0) scale(1)';
      el.style.transition = "all 1s cubic-bezier(.19,.93,.46,1.45)";
      done()
    },
    aEnter(el){
      // 上下动画
    },
    enterCanc(el){

    }
  },
  watch: {
    // '$route.path': function (newVal, oldVal) {
    //   console.log(newVal)
    //   // if(newVal === '/home'){
    //   //   document.getElementById('canBox').style.display = 'none'
    //   // }
    // }
  },
  directives: {
    focus: {
      inserted: function (el) {
        el.focus()
      }
    }
  }
}

</script>

<style lang="scss" scoped>
   #flBox{
     width: 100%;
     max-width: 750px;
     height: 100%;
     position: absolute;
     display: flex;
     justify-content: center;
     align-items: center;
     overflow: hidden;
   }
</style>



