    border-bottom: 1px solid #ccc;
    padding-left: 15px;
  }
  i{
    font-style: normal;
  }
  .sr-all{
    margin-right: 5px;
  }
  .circle{
    display: inline-block;
    width: 10px;
    height: 10px;
    border-radius: 100%;
  }
  .payStyle,.sellStyle,.cusStyle,.giveIntegralStyle,.useIntegralStyle{
    padding-bottom: 2%;
    border-bottom: 1px solid #aaa;
