import Vue from 'vue'
import Router from 'vue-router'
import login from '@/components/login.vue'
import index from '@/components/index.vue'

import log from '@/components/loginChild/log.vue'
import reg from '@/components/loginChild/reg.vue'
import home from '@/components/children/home.vue'
import makePic from '@/components/children/makePic.vue'
import echarts from '@/components/children/echarts.vue'
import richScan from '@/components/children/richScan.vue'
import errPage from '@/components/errPage.vue'

Vue.use(Router)

export default new Router({
  mode: 'history', // 去掉url中的#/
  routes: [
    {path: '/', redirect: '/login'},
    {
      path: '/login',
      component: login,
      children: [
        {path: 'log', component: log},
        {path: 'reg', component: reg}
      ],
      redirect: '/login/log'// 默认展示的子路由
    },
    {
      path: '/index',
      component: index,
      children: [
        // {path: '/home', components: { 'default': home, 'swiper': swiper }},
        {path: '/home', component: home},
        {path: '/makePic', component: makePic},
        {path: '/echarts', component: echarts},
        {path: '/richScan', component: richScan}
      ],
      redirect: '/home'// 默认展示的子路由
    },
    {path: '*', component: errPage}
  ],
  linkActiveClass: 'homeActive' // 覆盖默认类
})



