<template>
    <div>
      <el-carousel style="max-width: 750px" indicator-position="inside" :type="diyType" :height="countHeight">
        <el-carousel-item v-for="(val) in lunboList" :key="val.title">
          <img style="width: 100%" :src="val.url" :alt="val.title">
        </el-carousel-item>
      </el-carousel>
    </div>
</template>

<script>
// import s1 from './../../assets/images/s1.png'
// import s2 from './../../assets/images/s2.png'
// import s3 from './../../assets/images/s3.png'
export default {
  name: 'swiper',
  data () {
    return {
      lunboList: [
        {
          url: 'http://oking-electron.oss-cn-chengdu.aliyuncs.com/concat/s1.png?Expires=1658719748&OSSAccessKeyId=TMP.3KhKnbd4hgw517h8LqTbcpUF6arM6wdqvXDJbCttTyzUAw6GkAppd6KPkzvq6qxEjXHYs59i6PuY4G5VKHe2NHBAqerkXT&Signature=lN3kNccNacRb5nd2KClKZjazrLY%3D',
          title: '1'
        },
        {
          url: 'http://oking-electron.oss-cn-chengdu.aliyuncs.com/concat/s2.png?Expires=1658719763&OSSAccessKeyId=TMP.3KhKnbd4hgw517h8LqTbcpUF6arM6wdqvXDJbCttTyzUAw6GkAppd6KPkzvq6qxEjXHYs59i6PuY4G5VKHe2NHBAqerkXT&Signature=ZxGlwOsAIm2xLMCAzgfAVjdMN1o%3D',
          title: '2'
        },
        {
          url: 'http://oking-electron.oss-cn-chengdu.aliyuncs.com/concat/s3.png?Expires=1658719779&OSSAccessKeyId=TMP.3KhKnbd4hgw517h8LqTbcpUF6arM6wdqvXDJbCttTyzUAw6GkAppd6KPkzvq6qxEjXHYs59i6PuY4G5VKHe2NHBAqerkXT&Signature=BhR9UjD2BwUvBj%2FFl9H43jvYNVM%3D',
          title: '3'
        }
      ],
      countHeight: '',
      myType: ''
    }
  },
  created () {

  },
  mounted () {
    if (this.minVal === undefined) {
      this.countHeight = parseInt((this.$el.clientWidth * this.swPic.swHeight) / this.swPic.swWidth).toString() + 'px'
    } else {
      this.countHeight = parseInt((this.$el.clientWidth * this.swPic.swHeight) / (this.minVal * this.swPic.swWidth)).toString() + 'px'
    }
    this.$emit('setType', this.$el.clientHeight)// 调用父组件传递过来的方法，同时把数据传递出去
    // this.getLunbo()
  },
  props: ['diyType', 'swPic', 'minVal'],
  methods: {
    // getLunbo () {
    //   this.axios.get('vue-home').then(msg => {
    //     this.lunboList = msg.data.listArray
    //     console.log(msg)
    //   })
    // }
  }
}
</script>

<style lang="scss" scoped>
  .el-carousel{
    width: 100%;
    /*margin-bottom: 10px;*/
  }
  .el-carousel__item h3 {
    color: #475669;
    font-size: 15px;
    opacity: 0.75;
    /*line-height: 120px;*/
    margin: 0;
    padding: 0;
    text-align: center;
  }

  .el-carousel__item:nth-child(2n) {
    background-color: #99a9bf;
  }

  .el-carousel__item:nth-child(2n+1) {
    background-color: #d3dce6;
  }
</style>



