// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import router from './router'
import Vuex from 'vuex'
import axios from 'axios'
import MintUI from 'mint-ui'
import ElementUI from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css'
import 'mint-ui/lib/style.css'
import './../static/three.min'
import { Search, PullRefresh, Row, Col } from 'vant'

import App from './App'

Vue.use(Vuex)
Vue.use(MintUI)
Vue.use(ElementUI)
Vue.use(Search)
Vue.use(PullRefresh)
Vue.use(Row).use(Col)
Vue.config.productionTip = false
Vue.prototype.axios = axios
axios.defaults.baseURL = 'http://oking-electron.oss-cn-chengdu.aliyuncs.com/'

/* eslint-disable no-new */
const store = new Vuex.Store({
  state: {
    swHeight: 173,
    swWidth: 376
  },
  mutations: {
    changeType (state, parmsObj) {
      state.swHeight = state.swHeight / parmsObj.id
      state.swWidth = state.swWidth / parmsObj.id
    }
  },
  getters: {
    showType: function (state) {
      return state
    }
  }
})

const vm = new Vue({
  el: '#app',
  router,
  store,
  components: { App },
  template: '<App/>'
})



