<template>
  <div id="logBox">
      <input v-model="inpVal" ref="def" required='' maxlength="6" type='text' @blur="goToReg" @keyup.enter="goToReg">
      <label alt='请输入名称' placeholder='名称'></label>
  </div>
</template>

<script>
import { Toast } from 'mint-ui';
export default {
  name: 'login',
  data () {
    return {
      inpVal: ''
    }
  },
  methods: {
    goToReg () {
      let inpValue = this.inpVal.replace(/(^\s*)|(\s*$)/g, '')
      if (inpValue !== '' && inpValue != null) {
        localStorage.setItem('user_name', inpValue)
        this.$router.push('reg')
      } else {
        Toast({
          message: '请正确输入',
          position: 'bottom',
          duration: 3000
        });
        this.$refs.def.value = ''
      }
    }
  }
}
</script>

<style lang="scss" scoped>
  #logBox{
    width: 70%;
    margin-top: 70%;
  }
  input[type="text"] {
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
    width: 100%;
    height: 40px;
    margin: 0 0 13px;
    padding: 13px;
    border: 1px solid #ffd457;
    border-radius: 20px;
    background: #fafafa;
    resize: none;
    outline: none;
    box-shadow: 0 0 20px #ffd457;
    text-align: center;
  }
  input[type="text"][required]:focus {
    border-color: #ef952e;
    box-shadow:none;
  }
  input[type="text"][required]:focus + label[placeholder]:before {
    color: #dc832e;
    font-size: 13px;
  }
  input[type="text"][required]:focus + label[placeholder]:before,
  input[type="text"][required]:valid + label[placeholder]:before {
    -webkit-transition-duration: .1s;
    transition-duration: .1s;
    -webkit-transform: translate(0, -20px);
    -ms-transform: translate(0, -20px);
    transform: translate(0, -20px);
  }
  input[type="text"][required]:invalid + label[placeholder][alt]:before {
    content: attr(alt);
  }
  input[type="text"][required] + label[placeholder] {
    display: block;
    pointer-events: none;
    line-height: 14px;
    margin-top: calc(-39px - 2px);
    margin-bottom: calc(26px + 2px);
  }
  input[type="text"][required] + label[placeholder]:before {
    content: attr(placeholder);
    display: inline-block;
    margin: 0 calc(13px + 2px);
    padding: 0 2px;
    color: #aaa;
    white-space: nowrap;
    -webkit-transition: 0.2s ease-in-out;
    transition: 0.2s ease-in-out;
    background-image: -webkit-gradient(linear, left top, left bottom, from(#fafafa), to(#fafafa));
    background-image: -webkit-linear-gradient(top, #fafafa, #fafafa);
    background-image: linear-gradient(to bottom, #fafafa, #fafafa);
    background-size: 100% 2px;
    background-repeat: no-repeat;
    background-position: center;
  }
  label{
    text-align: center;
  }
</style>



