<template>
    <div class="errPageBox">
      <div class="errPage">
        <h1>请稍等</h1>
        <div>即将跳转到登录页...</div>
        <h3>{{ timeCount }}</h3>
      </div>
    </div>
</template>

<script>
export default {
  name: 'errPage',
  data () {
    return {
      timeCount: 3,
      timer: null
    }
  },
  mounted () {
    if (this.timer == null) {
      this.timer = setInterval(() => {
        this.setTime()
      }, 1000)
    }
  },
  methods: {
    setTime () {
      if (this.timeCount > 0) {
        this.timeCount --
      } else {
        this.$router.push('/')
        clearInterval(this.timer)
        this.timer = null
      }
    }
  },
  destroyed: function () {
    // 每次离开当前界面时，清除定时器
    clearInterval(this.timer)
    this.timer = null
  }
}
</script>

<style lang="scss" scoped>
  .errPageBox{
    width: 100%;
    height: 100%;
    position: fixed;
    z-index: 99;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #ff6a1b;
    .errPage{
      width: 60%;
      text-align: center;
    }
  }
</style>



