<template>
  <div id="wel">
    <div>
      <i>欢迎</i>
      <br><b>{{ userName }}</b>
      <br>正在进入。。。
    </div>
    <img src="../../assets/images/bg_content.png" alt="">
  </div>
</template>

<script>
export default {
  name: 'reg',
  data () {
    return {
      userName: localStorage.getItem('user_name')
      // bgUrl: require('@/static/img/bg_content.png')
    }
  },
  mounted () {
    setTimeout(() => {
      this.goIndex()
    }, 3000)
  },
  methods: {
    goIndex () {
      this.$router.push({path: '/index'})
      // this.$router.replace({path: '/index'})
    }
  }
}
</script>

<style lang="scss" scoped>
   #wel{
     width: 100%;
     position: absolute;
     text-align: center;
     img{
       width: 50%;
       max-width: 250px;
       margin:0 25% 0 25%;
     }
     div{
       width: 100%;
        position: absolute;
       top: 50px;
       text-align: center;
       font-size: 15px;
       color: #999;
     }
     i{
       font-style: normal;
       font-size: 14px;
     }
     b{
       padding: 1px 0;
       font-size: 18px;
       display: inline-block;
       color: #ef952e;
     }
   }

</style>



