<template>
  <div>
    <v-swiper ref="swTall" v-bind:swPic="swHeights" v-on:setType="setTypeFn"></v-swiper>
    <form action="/">
      <van-search
        v-model="value"
        placeholder="请输入卡号"
        show-action
        @search="onSearch"
        @cancel="onCancel"
        ref="inp"
        @focus="setTop"
      />
    </form>
    <van-pull-refresh ref="refreshBox" :style="'overflow:scroll;height:'+swTall+'px'" v-model="isLoading" @refresh="onRefresh">
      <p v-for="item in cardUseList" :key="item.card">
        <van-row>
          <van-col span="12">卡号：<span class="jin">NO.{{ item.card }}</span></van-col>
          <van-col span="12" style="text-align: right">
            <i :class="['icon', 'iconfont', 'icon-zhuanshi', item.name === '金卡' ? 'jin' : 'yin']"></i>
            <span>{{ item.name }}</span>
          </van-col>
        </van-row>
        <van-row>
          <van-col span="12">消费日期：{{ item.date }}</van-col>
          <van-col span="12">消费项目：{{ item.items }}</van-col>
        </van-row>
        <van-row>
          <van-col span="12">单据号：{{ item.orderNumber }}</van-col>
          <van-col span="12">消费人次：{{ item.times }}</van-col>
        </van-row>
      </p>
      <div class="downTips">下拉刷新</div>
    </van-pull-refresh>
  </div>
</template>

<script>
import swiper from '@/components/common/swiper.vue'
import 'vant/lib/search/style'
import 'vant/lib/pull-refresh/style'
import 'vant/lib/row/style'
import 'vant/lib/col/style'
export default {
  name: 'home',
  data () {
    return {
      fullscreenLoading: false,
      newsList: [],
      count: 0,
      swHeights: this.$store.getters.showType,
      value: '',
      isLoading: false,
      swTall: 0,
      cardUseList: [
        {
          name: '金卡',
          card: '111001',
          date: '2018.1.01',
          items: '娱乐城',
          orderNumber: '11111001',
          times: 2
        },
        {
          name: '银卡',
          card: '222002',
          date: '2018.1.02',
          items: '万象城',
          orderNumber: '11111002',
          times: 3
        },
        {
          name: '金卡',
          card: '333003',
          date: '2018.1.03',
          items: '水上公园',
          orderNumber: '11111003',
          times: 2
        },
        {
          name: '金卡',
          card: '444004',
          date: '2018.1.04',
          items: '海上公园',
          orderNumber: '11111004',
          times: 1
        },
        {
          name: '银卡',
          card: '555005',
          date: '2018.1.05',
          items: '动物园',
          orderNumber: '11111005',
          times: 5
        }
      ]
    }
  },
  created () {
    this.openFullScreen()
    // this.getNews()
    localStorage.setItem('newsList', JSON.stringify(this.cardUseList))
    localStorage.setItem('newsListCopy', JSON.stringify(this.cardUseList))
  },
  components: {
    'v-swiper': swiper
  },
  mounted () {
    this.init()
    let swHei = parseInt((this.$el.clientWidth * this.swHeights.swHeight) / this.swHeights.swWidth)
    // console.log(this.$root.$el.clientHeight)
    this.swTall = this.$root.$el.clientHeight - swHei - 54 - 50 - 40

  },
  directives: {
  },
  methods: {
    init () {
      // this.$store.commit('changeType')
    },
    setTypeFn (val) {
      console.log('faGet' + val)
    },
    onSearch () {

    },
    onCancel () {

    },
    setTop () {
      // document.getElementsByClassName('mint-tabbar')[0].style.display = 'none'
    },
    onRefresh () {
      this.value = ''
      for (let index of new Array(6).fill(1).keys()) {
        setTimeout(() => {
          this.$toast('刷新成功')
          this.isLoading = false
          let addObj = {}
          addObj['name'] = (Math.random() * 10) > 5 ? '金卡' : '银卡'
          addObj['card'] = Math.floor((Math.random() * 100000000)).toString()
          addObj['date'] = '2018.1.05'
          addObj['items'] = (Math.random() * 10) > 5 ? '购物商场' : '花园'
          addObj['orderNumber'] = '1111100' + Math.floor((Math.random() * 100) + 1)
          addObj['times'] = parseInt(index)
          this.cardUseList.unshift(addObj)
          let addData = JSON.parse(localStorage.getItem('newsListCopy'))
          addData.push(addObj)
          localStorage.setItem('newsListCopy', JSON.stringify(addData))
        }, 1000)
      }
    },
    getNews () {
      // this.axios.get('vue-home-news').then(msg => {
      //   // this.newsList=msg.data.newsList
      //    console.log(msg.body.data.newsList)
      // })
    },
    openFullScreen () {
      const loading = this.$loading({
        lock: true,
        text: 'Loading',
        spinner: 'el-icon-loading',
        background: 'rgba(0, 0, 0, 0.7)'
      });
      setTimeout(() => {
        loading.close();
      }, 1000);
    }
  },
  beforeUpdate () {

  },
  watch: {
    cardUseList (newVal) {
      // console.log(newVal)
      localStorage.setItem('newsList', JSON.stringify(this.cardUseList))
    },
    value (newVal, oldVal) {
      let newsList = JSON.parse(localStorage.getItem('newsListCopy'))
      this.cardUseList = []
      for (let [i, val] of newsList.entries()) {
        if (val['card'].indexOf(newVal) > -1) {
          this.cardUseList.unshift(val)
        }
      }
    }
  }
}
</script>

<style lang="scss" scoped>
  @import "../../assets/fonts/iconfont.css";
  .jin{
    color: #ffc666;
    vertical-align: top!important;
  }
  .yin{
    color: #bbb;
    vertical-align: top!important;
  }
  .downTips{
    padding: 5px 0;
    text-align: center;
    font-size: 14px;
    color: #999;
  }
  p{
    background-color: #fff;
    padding: 5px 10px;
    box-sizing: border-box;
    border-bottom: 10px solid #eee;
    border-top: 1px solid #eee;
    .van-row:first-of-type{
      padding: 8px 0;border-bottom: 1px solid #ddd
    }
    .van-row:nth-of-type(2){
      font-size: 15px;padding: 8px 0 4px 0;color:#666
    }
    .van-row:last-of-type{
      font-size: 15px;padding: 4px 0 6px 0;color:#666
    }
    &:last-of-type{
      border-bottom: none;
    }
  }
</style>



