<template>
    <transition style="width: 100%;height: 100%;max-width: 750px;">
      <router-view></router-view>
    </transition>
</template>

<script>
export default {
  name: 'App',
  data () {
    return {
    }
  },
  watch: {
    '$route.path': function (newVal, oldVal) {
      let canBox = document.getElementById('canBox')
      console.log('监听到了' + newVal)
      if (newVal === '/login/log' || newVal === '/login/reg') {
        canBox.style.display = 'block'
      } else {
        canBox.style.display = 'none'
      }
    }
  }
}
</script>

<style scoped lang="scss">
  body{
    overflow: hidden;
  }
  .v-enter{
    opacity: 0;
    transform:translateX(100%) scale(0.8);
  }
  .v-leave-to{
    opacity: 0;
    transform:translateX(-100%);
    position:absolute;
    top:60px;
  }
  .v-enter-active, .v-leave-active{
    transition:all 0.6s ease;
  }
</style>



